/**
 * object()
 * @param {Object}      obj     obejct
 * @return {boolean}
 * @method isEmpty
 */
function isEmpty(obj) {
    for (var x in obj) {
        if (obj.hasOwnProperty(x)) return false;
    }
    return true;
}
/**
 * 2JSON
 * @param {Object}      json1   obejct
 * @param {Object}      json2   obejct
 * @return {boolean}
 * @method jsonEqual
 */
function jsonEqual(json1, json2) {
    return JSON.stringify(json1, null, 1) == JSON.stringify(json2, null, 1);
}
/**
 * JSON
 * @param {Object}      json   json
 * @return {Object}
 * @method clone
 */
function clone(json) {
    return JSON.parse(JSON.stringify(json));
}
